/* cover */

.cover {
  display: flex;
  flex-direction: row;
  height: 100vh;
  opacity: 0;
  transition: opacity .5s .25s ease-out;
  padding-top: 25px;
  background-color: #ffffff;
}

.cover.is-visible {
    opacity: 1;
}

.cover-container-text {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 0px;
    text-align: left;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    transform: translateY(25px);
}

.cover-title {
    font-size: 1.6rem;
    color: #c53a13;
    letter-spacing: 1px;
    font-weight: 800;
    transition: transform 10s .25s cubic-bezier(0,1,.3,1);
    transition-delay: 200ms;
    padding-bottom: 10px;
}

.cover-title.is-visible {
    transform: translate(0px, -25px);
}

.cover-paragraph {
    font-size: 3.4rem;
    line-height: 41px;
    transition: transform 10s .25s cubic-bezier(0,1,.3,1);
    transition-delay: 250ms;
}

.cover-paragraph.is-visible{
    transform: translate(0px, -25px);
}

.cover-social {
    display: flex;
    margin-top: 50px;
    transition: transform 10s .25s cubic-bezier(0,1,.3,1);
}

