/* Projects section */

.image-project {
    text-align: center;
    position: relative;
    opacity: 0;
    transition: opacity .5s .25s ease-out;
}

.image-project-right {
    text-align: right;
    position: relative;
    opacity: 0;
    transition: opacity .5s .25s ease-out; 
}

.image-project-right.is-visible {
    opacity: 1;
}

.image-project.is-visible {
    opacity: 1;
}

.image-projects {
    width: 95%;
}

.title-project {
    position: absolute;
    font-size: 3.5rem;
    top: -5%;
    left: 45%;
    transition: transform 10s .25s cubic-bezier(0,1,.3,1);
}

.title-project.is-visible {
    transform: translate(0px, -15px);
    transition-delay: 300ms;
}

.title-project-left {
    position: absolute;
    font-size: 3.5rem;
    top: 2%;
    left: 5%;
    transition: transform 10s .25s cubic-bezier(0,1,.3,1);
}

.title-project-left.is-visible {
    transform: translate(0px, -15px);
    transition-delay: 300ms;
}

.link-project {
    position: absolute;
    top: 6%;
    left: 60%;
    font-weight: 700;
    text-decoration: none;
    text-transform: capitalize;
    font-size: 1.5rem;
    transition: transform 10s .25s cubic-bezier(0,1,.3,1);
}

.link-project.is-visible {
    transform: translate(0px, -15px);
    transition-delay: 500ms;
}

.link-project:hover {
    text-decoration: underline;
}

.link-project-left {
    position: absolute;
    top: 12%;
    left: 32%;
    font-weight: 700;
    text-decoration: none;
    text-transform: capitalize;
    font-size: 1.5rem;
    transition: transform 10s .25s cubic-bezier(0,1,.3,1);
}

.link-project-left.is-visible {
    transform: translate(0px, -15px);
    transition-delay: 500ms;
}

.link-project-left:hover {
    text-decoration: underline;
}


.grey-color {
    color: #242424;
}



/* Media Queries for IPAD */

@media (min-width: 768px) and (max-width: 1024px) {

    .mobile-container-list {
        padding-top: 280px;
    }

    // .cover-container-text {
    //     padding-left: 0px;
    // }

    .title-project {
        font-size: 5.4rem;
        left: 60%;
        top: 5%;
    }

    .title-project-left {
        font-size: 5.4rem;
        top: 5%;
        left: 5%;
    }

    .link-project {
        top: 14%;
        left: 55%;
        font-size: 2rem;
    }

    .link-project-left {
        top: 14%;
        left: 47%;
        font-size: 2rem;
    }
  }

  /* Media Queries for Desktop */

  @media (min-width: 1025px) and (max-width: 1280px) {

    // .cover-container-text {
    //     padding-left: 60px;
    // }

    .cover-paragraph {
        font-size: 3.8rem;
        padding-right: 100px;
        line-height: 54px;
    }

    .title-project {
        font-size: 5.4rem;
        left: 65%;
        top: 5%;
    }

    .title-project-left {
        font-size: 5.4rem;
        top: 5%;
        left: 5%;
    }

    .link-project {
        top: 15%;
        left: 62%;
        font-size: 2rem;
    }

    .link-project-left {
        top: 16%;
        left: 37%;
        font-size: 2rem;
    }

    .image-projects {
        width: 60%;
    }
  }

  @media (min-width: 1281px) {

    .title-project {
        font-size: 5.4rem;
        left: 65%;
        top: 5%;
    }

    .title-project-left {
        font-size: 5.4rem;
        top: 5%;
        left: 5%;
    }

    .link-project {
        top: 13%;
        left: 62%;
        font-size: 2rem;
    }

    .link-project-left {
        top: 13%;
        left: 27%;
        font-size: 2rem;
    }

    .image-projects {
        width: 65%;
    }
  }


  /* KeyFrames */

  @keyframes opacity {
    0% {
      transform: translate(100px, -250px);
      opacity: 0;
    }
    50%{
      transform: translate(0px, 0px);
      opacity: 0.5;
    }
    100% {
      transition: transform 0.2s;
      opacity: 1;
    }
  }


/* Main */

.title {
    padding: 150px 0px 20px 0px;
    text-align: center;
    font-size: 5rem;
    color: #242424;
}

.tag-projects {
    position: absolute;
    z-index: 1;
    padding: 10px;
    left: 1.5%;
    font-weight: 800;
    color: white;
    opacity: 0.4;
    text-transform: capitalize;
}

.black-tag {
    color: #242424;
}

.links-container {
    display: flex;
    flex-wrap: wrap;
    padding: 8px;
    justify-content: center;
}

.link-category {
    text-decoration: none;
    font-size: 1.2rem;
    margin-right: 10px;
    padding: 5px 10px 5px 5px;
}

.link-category:hover {
    font-weight: 700;
}

.projects-container {
    display: flex;
    flex-wrap: wrap;
    margin: 10px 0px 40px 0px;
    justify-content: center;
    text-align: center;
}

.project-cover {
    margin: 2px -5px 2px -5px;
    position: relative;
    opacity: 0;
    transition: opacity .5s .25s ease-out;
    margin-bottom: 1.5%;
}

.project-cover.is-visible {
    opacity: 1;
}


.project-image {
    width: 97%;
}


.project-image:hover {
    opacity: 0.5;
}

.project-text-container {
    position: absolute;
    z-index: 1;
    color: #242424;
    top: 0;
    bottom: 0;
    right: 10px;
    left: 9px;
    margin: auto;
    transition: opacity 350ms linear;
    opacity: 0;
}


.project-text-container:hover {
    position: absolute;
    z-index: 1;
    top: 0;
    bottom: 0;
    right: 10px;
    left: 9px;
    margin: auto;
    opacity: 1;
}

.second-image {
    width: 100%;
    height: 100%;
}

